/*******************************************************************************
 *  
 * Circular Motion
 */

export default (canvas)=>{

  circularMotion()
  window.addEventListener('resize', ()=> circularMotion() )

  function circularMotion(){

  const ctx = canvas.getContext('2d')
  let w = canvas.width = canvas.offsetWidth
  let h = canvas.height = canvas.offsetHeight

  const mouse = {
    x: w/2,
    y: h/2
  }

  const colors =[
    '#2185C5',
    '#7ECEFD',
    '#FFF6E5',
  ]

  canvas.addEventListener('mousemove', (e)=>{
    mouse.x = e.clientX
    mouse.y = e.clientY
  })

  canvas.addEventListener('resize', ()=>{
    w = canvas.width = canvas.offsetWidth
    h = canvas.height = canvas.offsetHeight
    init()
  })

  function randomIntFromRange(min, max){
    return Math.floor(Math.random()*(max-min+1)+min)
  }

  function randomColor(){
    return colors[Math.floor(Math.random()*colors.length)]
  }

  function Particle(x, y, radius, color){
    this.x = x
    this.y = y
    this.radius = radius
    this.color = color
    this.radian = Math.random()*Math.PI*2
    this.velocity = 0.005
    this.distanceFromCenter = randomIntFromRange(50, 100)

    this.update=()=>{
      const lastPoint = {
        x:this.x,
        y:this.y
      }

      this.radian += this.velocity
      this.x = x + Math.cos(this.radian) *
        this.distanceFromCenter
      this.y = y + Math.sin(this.radian) *
        this.distanceFromCenter
      this.draw(lastPoint)
    }

    this.draw=(lastPoint)=>{
      ctx.beginPath()
      ctx.strokeStyle = this.color
      ctx.lineWidth = this.radius
      ctx.moveTo(lastPoint.x, lastPoint.y)
      ctx.lineTo(this.x, this.y)
      ctx.stroke()
      ctx.closePath()
    }
  }

  let particles
  function init(){
    particles = []
    for (let i=0; i<14;i++){
      const radius = (Math.random() * 800) +1
      particles.push(
        new Particle(w/2, h/2, radius, randomColor(colors))
      )
    }
  }

  function loop(){
    requestAnimationFrame(loop)
    ctx.fillStyle=`rgba(255,255,255, 0.005)`
    ctx.fillRect(0, 0, w, h )

    ctx.fillText('CANVAS', mouse.x, mouse.y)

    particles.forEach( particle => {
      particle.update()
    })
  }

  init()
  loop()
} // end circularMotion()
}
