import React, {useEffect, useState} from 'react'
import styles from './canvas.module.scss'

export default ({ jsCode, isHome })=>{
  let mount
  const [isBig, setBig] = useState(false)

  useEffect(()=>{
    jsCode(mount)
  })
  return <canvas
    className={isBig ? styles.big : styles.mini}
    ref={ el=>mount=el }
    onClick={()=>setBig(!isBig)}
  />
}
